/*헤더*/

@media (max-width: 1300px) {
  header.header_d{
    font-size: 16px;
    word-break: break-all
  }
}

header.header_d{
  position: absolute;
  top: 0px;
  z-index: 999;
  display: flex;
  height: 80px;
  width: 100%;
  align-items: center;
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  font-size: 1.125rem;
  line-height: 1.75rem;
  font-weight: 500;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms
}

@media (min-width: 768px){
  header.header_d{
    --tw-text-opacity: 1;
    color: rgb(255 255 255 / var(--tw-text-opacity))
  }
}

@media (min-width: 960px){
  header.header_d{
    position: fixed;
    background-color: transparent
  }
}

@media (max-width: 1300px) {
  header{
    font-size: 16px;
    word-break: break-all
  }
}

header{
  position: fixed;
  top: 0px;
  z-index: 999;
  display: flex;
  height: 60px;
  width: 100%;
  align-items: center;
  border-radius: 0px;
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  padding-top: 0.625rem;
  padding-bottom: 0.625rem;
  font-size: 1.125rem;
  line-height: 1.75rem;
  font-weight: 500;
  --tw-text-opacity: 1;
  color: rgb(19 26 21 / var(--tw-text-opacity));
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms
}

@media (min-width: 960px){
  header{
    position: fixed
  }
}

header.on{
  position: fixed;
  height: 60px;
  border-radius: 0px;
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  padding-top: 0.625rem;
  padding-bottom: 0.625rem;
  --tw-text-opacity: 1;
  color: rgb(19 26 21 / var(--tw-text-opacity))
}

header.on.header_d a.logo{
  height: 50px
}

header.on.header_d .logo_l_e{
  display: block
}

header.on.header_d .logo_d_e{
  display: none
}

header.on nav ol{
  top: 70px
}

@media (min-width: 768px){
  header.on nav ol{
    top: 0px
  }
}

nav ol{
  position: absolute;
  top: 80px;
  margin: 0 0 0 auto;
  height: 100vh;
  width: 100%;
  --tw-translate-x: 100%;
  transform: translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
  flex-direction: column;
  justify-content: space-between;
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  padding-left: 0px !important;
  padding-right: 0px !important;
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms
}

@media (min-width: 768px){
  nav ol{
    position: relative;
    top: 0px;
    margin: auto;
    display: flex;
    height: 100%;
    max-width: 1300px;
    --tw-translate-x: 0px;
    transform: translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
    flex-direction: row;
    background-color: transparent
  }
}

nav ol.on{
  height: 100vh;
  --tw-translate-x: 0px;
  transform: translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity))
}

@media (min-width: 768px){
  nav ol.on{
    height: 100%
  }
}

nav ol li{
  border-bottom-width: 3px;
  border-style: solid;
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  text-align: center;
  line-height: 73px
}

@media (min-width: 768px){
  nav ol li{
    max-width: 16.66667%;
    flex: 0 0 16.66667%;
    flex-grow: 1;
    border-bottom-width: 0px;
    background-color: transparent
  }
}

nav ol li a{
  display: flex;
  height: 60px;
  align-items: center;
  justify-content: center;
  text-align: center;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms
}

nav ol li a:hover{
  --tw-text-opacity: 1;
  color: rgb(5 205 130 / var(--tw-text-opacity))
}

