}

h1,.h1{
  font-size: 3.75rem;
  line-height: 1;
  line-height: 150%
}

h2,.h2{
  font-size: 3rem;
  line-height: 1;
  line-height: 150%
}

h3,.h3{
  font-size: 2.25rem;
  line-height: 2.5rem;
  line-height: 150%
