@media (max-width: 1300px) {
  h1, .h1 {
    font-size: calc(3rem + 0.5vw) !important;
  }

  h2, .h2 {
    font-size: calc(2.25rem + 0.5vw) !important;
  }

  h3, .h3 {
    font-size: calc(1.875rem + 0.5vw) !important;
  }

  h4, .h4 {
    font-size: calc(1.5rem + 0.5vw) !important;
