/* 햄버거 바 */

.menu_icon{
  position: relative;
  display: inline-block;
  height: 3px;
  width: 40px;
  border-radius: 0.75rem;
  --tw-bg-opacity: 1;
  background-color: rgb(19 26 21 / var(--tw-bg-opacity));
  font-size: 50px;
  line-height: 80px
}

@media (min-width: 960px){
  .menu_icon{
    --tw-bg-opacity: 1;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity))
  }
}

.menu_icon:before{
  position: absolute;
  top: -0.75rem;
  left: 0px;
  display: inline-block;
  height: 3px;
  width: 40px;
  border-radius: 0.75rem;
  --tw-bg-opacity: 1;
  background-color: rgb(19 26 21 / var(--tw-bg-opacity));
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
  --tw-content: '';
  content: var(--tw-content)
}

@media (min-width: 960px){
  .menu_icon:before{
    --tw-bg-opacity: 1;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity))
  }
}

.menu_icon:after{
  position: absolute;
  bottom: -0.75rem;
  left: 0px;
  display: inline-block;
  height: 3px;
  width: 40px;
  border-radius: 0.75rem;
  --tw-bg-opacity: 1;
  background-color: rgb(19 26 21 / var(--tw-bg-opacity));
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
  --tw-content: '';
  content: var(--tw-content)
}

@media (min-width: 960px){
  .menu_icon:after{
    --tw-bg-opacity: 1;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity))
  }
}

header.on .menu_icon, header.on .menu_icon:before, header.on .menu_icon::after{
  --tw-bg-opacity: 1;
  background-color: rgb(19 26 21 / var(--tw-bg-opacity))
}

.btn_menu_btn.on .menu_icon{
  background-color: transparent
}

.btn_menu_btn.on .menu_icon:before{
  top: 0px;
  --tw-rotate: 45deg;
  transform: translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y))
}

.btn_menu_btn.on .menu_icon:after{
  bottom: 0px;
  --tw-rotate: -45deg;
  transform: translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y))
}

.tag{
  display: flex;
  align-items: center;
  border-radius: 3px;
  --tw-bg-opacity: 1;
  background-color: rgb(0 191 150 / var(--tw-bg-opacity));
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  text-align: center;
  font-size: 0.875rem !important;
  line-height: 1.25rem !important;
  font-weight: 700;
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity));
  --tw-shadow: rgba(19, 26, 21, 0.15), 0px 1px 2px rgba(4, 185, 117, 0.3);
