/* 서브 1 */

.ourService{
  position: relative;
  display: flex;
  min-height: 350px;
  width: 100%;
  max-width: 100%;
  align-items: center;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms
}

.ourService::after{
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 0;
  display: block;
  height: 100%;
  width: 0px;
  background-color: rgb(0 0 0 / 0.8);
  --tw-content: '';
  content: var(--tw-content)
}

.ourService:hover{
  border-width: 5px;
  --tw-border-opacity: 1;
